@import url(https://fonts.googleapis.com/css2?family=Lato&family=Quicksand&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: none!important;
    box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
    font-family: 'Quicksand', sans-serif;
}

table .buttonsDesktop{
    display: block;
}


.menu-lateral{
    position: fixed;
    top: 0;
    width: 13%;
    height: 100vh;
    background-color: teal;
    box-shadow: 0px 10px 0px 0px rgb(0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 14px;
    overflow-y:auto;
}

.menu-lateral a{
    color: white;
    transition: all .2s ease;
}

.menu-lateral a:hover{
    text-decoration: none;
    font-weight: bold;
}

body{
    background-color: #e1dfdf;
    overflow-x: hidden;
}

.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.fast {
    -webkit-animation-duration: 0.4s;
    animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.fadeIn {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
}


@media(max-width:768px){
    .menu-lateral{
        width: 40%;
        left: -100%;
        transition: all .5s ease;
    }

    .menu-lateral.show{
        left: 0;
    }
}

@media(min-width:768px){
    .menu-lateral{
        padding-top: 200px;
    }
}   

@media (min-width:1400px) {
    .menu-lateral{
        padding-top: 0px;
    }
}
.dropdown-menu{
    left: -70px !important;
}

.box__balance{
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 5px;
    padding: 10px 15px;
    box-shadow: 0 2px 5px rgb(163, 161, 161);
    transition: all .5s ease;
    margin: 10px 0px;
    position: relative;
}

.box__balance:hover{
    box-shadow: 0 4px 10px rgb(163, 161, 161);
}

.box__balance .box__icon{
    width: 22%;
    background-color: teal;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 17px;
    border-radius: 50%;
}

.box__balance .box__avatar img{
    width: 50px;
    height: 50px;
    border-radius: 25px;
}

.box__balance .box__data{
    padding: 0px 0px 0px 15px;
}

.box__balance .box__data h4{
    margin: 0;
    font-weight: bold;
}

.box__balance .box__data span{
    text-transform: uppercase;
    font-size: 13px;
    font-weight: bold;
}

.box__balance .box__detalle{
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 13px;
    text-transform: uppercase;
    font-weight: bold;
}

.box__balance .box__detalle.warning{
    color: #f7cc20;
}

.box__balance .box__detalle.success{
    color: teal;
}

.lista__usuarios{
    padding: 0px 20px;
    margin: 10px 0px;
}

.lista__usuarios .lista__body{
    max-height: 340px;
    overflow-y: auto;
    padding: 2px 5px;
}

.lista__usuarios .lista__body::-webkit-scrollbar{
    display: none;
}

.lista__usuarios #btnReload{
    cursor: pointer;
    display: block;
    padding: 10px;
    background-color: teal;
    color: white;
    border-radius: 50%;
}
#btn__circle{
    border:none;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    font-size: 12px;
    box-shadow: 0 2px 5px rgb(163, 161, 161);
    transition: all .5s ease;
}

#btn__circle:hover{
    box-shadow: 10px 10px 20px rgb(163, 161, 161);
    font-weight: 13px;
}

#btn__circle.success{
    background-color: teal;
    color: white;
}

#btn__circle i{
    display: block;
    font-size: 36px;
    margin-bottom: 10px;
}
.Modal{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0px;
    display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgba(7, 7, 7, 0.596);
    z-index: 2000;
    transition: all 1s ease;
    padding: 50px 0px 0px 0px;
}

.Modal .modal-dialog{
    width: 80%;
    position: relative;
    top:-1000px;
    transition: all .7s ease;
    height: auto!important;
    background-color: white;
    padding: 10px 16px 10px 16px;
    border-radius: 13px;
}

.Modal .modal-content{
    border:none!important;
}

.Modal .modal-dialog.show{
    top: 0px;
}

.Modal .modal-dialog i{
    position: absolute;
    top: -25px;
    background-color: #FFB347;
    right: -22px;
    padding: 10px;
    color: white;
    border-radius: 11px;
}

.Modal .modal-dialog{
    max-width: 55%!important;
}

@media(max-width:768px){
    .Modal .modal-dialog{
        max-width: 100%!important;
        height: 85vh!important;
    }
    .Modal .modal-dialog .modal-content{
        height: 100%;
    }
}
.myForm {
    height: calc(100vh - 62px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.myForm form{
    border-radius: 20px;
}
.table-envios tr.pago{
    background-color: #19578a;
    color: #FAFAFA;
}

.table-envios tr.nopago{
    background-color: #535252;
    color: white;
}
.table-envios thead, .table-envios tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.table-envios td, .table-envios th{
    border:none!important
}

.table-envios tr{
    border-radius:16px;
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.25);
    margin: 20px 0px;
}

.table-envios tbody {
    display: block;
    height: 568px;
    overflow: auto;
    padding: 10px;
}

@media(max-width:768px){
    .table-envios tbody{
        height: calc(100vh - 200px);
    }
    .table-envios thead{display: none!important;}

    .table-envios thead, .table-envios tbody tr {
        display: grid;
    }
    
    .table-envios .buttonsDesktop{
        display: flex;
        justify-content: space-around;
    }

    .table-envios .span-pagado{
        position: relative;
        top: 50px;
        display:block;
        width:10px;
        height:10px;
        background-color:#439443;
        border-radius:5px;
    }
}

.detalleVenta{
    overflow-y: auto;
}
.detalleVenta ul{
    border-bottom:solid 3px #FFB347;
    list-style: none;
}

.datosComprador{
    display: flex;
    align-items: center;
}

.datosComprador p{
    margin: 0;
    font-size: 23px;
    font-family: 'Quicksand', sans-serif;
}

.detalleVenta .datosComprador .fotoUsuario{
    height: 50px;
    border-radius: 25px;
}

.detalleVenta span{font-weight: bold;}

@media(max-width:768px){
    .datosComprador{
        justify-content: space-between;
    }
    .detalleVenta h3{font-size: 20px;}
    .detalleVenta ul{
        font-size: 13px;
        padding-left: 0px!important;
    }

    .detalleVenta table{font-size: 13px;}
}
#btn-mostrarFiltro{
    display: none;
}
@media(max-width:768px){
    #formFiltroEnvios{
        transition: all 1s ease;
        display: none;
    }

    #formFiltroEnvios.show{
        display: block;
    }
    #formFiltroEnvios section{
        margin:15px 0px
    }

    #btn-mostrarFiltro{
        display: block;
        position: absolute;
        right: 28px;
        top: 86px;
    }
}
